

.container {
    width: min(100% - 30px, 1280px);
    margin-inline: auto;
}

header {
    height: 60px;
}

body {
    background-color: #fff;
}

h1 {
    width: 40%;
    font-size: 40px;
    font-weight: 600;
    color: olive;
    /* margin-right: 30px; */
    line-height: 60px;
}

span {
    color: purple;
}

nav {
    width: 60%;
    text-align: right;
    font-size: 24px;
    color: olive;
    /* margin-right: 30px; */
    line-height: 60px;
    font-weight: 600;

}

.flexbox {
    display: flex;
}

nav ul li {
    display: inline;
    list-style: none;
    margin-right: 50px;
    text-decoration: none;
}

nav ul li:last-child{
    margin-right: 0px;

}

nav ul li a {
    text-decoration: none;
    color: olive;
}

nav ul li a:hover {
    color: #f9004d;
}

section {
   
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, 1fr);
    gap: 20px;
}

.sort{
    display: right;
}

.sort-div{
    text-align: right;
}

button{
    height: 50px;
    margin-top: 20px;
    text-align: center;
    width: 200px;
    border-radius: 6px;
    font-size: 24px;
    margin-right: 10px;
    margin-bottom: 20px;
}

  
 
  

  .img-name{
    display: flex;

  }

  .left{
    width: 70%;
    text-align: left;
  }
  .right{
    width: 30%;
    text-align: right;
  }
  
  a{
    text-decoration: none;
  }

  figure img {
    border-radius: 5px;

}

.movie-item {
    padding: 3px;
    border: 2px solid gray;
    text-decoration: none;
    transition: all 0.3s ease-in-out;
    padding: 5px;
   
}

.movie-item:hover {
    border-color: gray;
    cursor: pointer;
}
.detail-row{
}
.poster-detail {
    margin-right: 20px;
  
}
.column-2 ul{
    display: flex;
    margin-top: 0px;
}

.column-2 .name{
    font-size: 24px;
    color: #191970;
    font-weight: 600;
}
.ul li{
    margin-right: 20px;
    margin-left: 20px;
    font-size: 14px;
    color:#2f4f4f;

}


.column-2 .brand{
    font-size: 24px;
    color: lightsalmon;
}

.desc{
    width: 600px;
    margin-top: 20px;
    color: #333;
    font-weight: 600;
}
.name{
    margin-top: 0px;
}

.buy{
    width: 80px;
    font-size: 14px;
  background-color:   #4169e1;
  color: #fff;
}
.poster-detail figure img {
    background: transparent;
  }

  .buy:hover{
    background-color: #191970;
  }
